import { ToastService } from './../../../services/toast.service';
import { LoaderService } from 'src/app/services/loader.service';
import { Component, OnInit } from '@angular/core';
import { ApiProgramsService } from 'src/app/services/api-programs.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { environment } from 'src/environments/environment.prod';
import { ApiGeneralService } from 'src/app/services/api-general.service';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-career-plan',
  templateUrl: './career-plan.page.html',
  styleUrls: ['./career-plan.page.scss'],
})
export class CareerPlanPage implements OnInit {
  webAssetsPath = "";
  arrInterestedPrograms = [];  
  progInterestId="";
  currentSelected=0;
  SubSelectedTab="a1";

  AssetsPathVideo = environment.webAssetsPath+ "mobility-app/videothumbnails";
  arrAllVideos = [];
  arrVideoMBBS = [];

  isVideoOpen : boolean ;
  iframe : any;

  slideVideoOpts = {
    slidesPerView: 1.5,
    freeMode: true,
    coverflowEffect: {
      rotate: 50,
      stretch: 0,
      depth: 100,
      modifier: 1,
      slideShadows: true,
    }
  }
  constructor(
    private headerTitle: HeaderTitleService,
    private apiProgramsService: ApiProgramsService,
    private loader : LoaderService,
    private apiGeneralService : ApiGeneralService,
    private sanitizer : DomSanitizer,
    private toastr : ToastService
  ) {
    this.headerTitle.isBack = false;
    this.headerTitle.title = "Career Plan";
  }
  ionVIewWillEnter(){
    this.SubSelectedTab="a1";
    this.currentSelected=0;
  }

  ngOnInit() {    
    this.getIntersetedProgram();
    this.getMBBSVideos();
  }
  getIntersetedProgram(){  
    this.loader.presentLoading();
    var degreeID = 1;  
    let postData = {"DegreeId": degreeID,"IsActive": true}
    this.apiProgramsService.getInterestedProgram(JSON.stringify(postData)).then(resp => {      
      this.arrInterestedPrograms  = resp;    
      this.webAssetsPath = environment.webAssetsPath + "prog-interest/images"; 
      this.currentSelected = this.arrInterestedPrograms[0]["progInterestId"];
      this.loader.loadingDismiss();     
      
    })
    .catch(err => {
      console.log("getInterestedPrograms: " + err); 
      this.loader.loadingDismiss();        
    })
  }

  ionSelectProgram(progInterestId, index){
    this.progInterestId = progInterestId;
    this.currentSelected = progInterestId;
    if(progInterestId == 1){
      this.SubSelectedTab = "a1";      
    }    
    else if(progInterestId = 3){
      this.SubSelectedTab = "c1";
    }
    else if(progInterestId = 2){
      this.SubSelectedTab = "b1";
    }
  }

  subTab(subTabId){
    this.SubSelectedTab = subTabId;   
  }

  //== Video Section start
  async getMBBSVideos(){
    let postData = {'videoHeaderId': 2, 'isActive': true}
    await this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {      
      this.arrVideoMBBS  = resp;
    })
    .catch(err => {
      console.log("getVideos: " + err);           
    })
  }

  playVideo(videoSrc){
    if(videoSrc == ""){
      this.toastr.toastError("Sorry! Currently this video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="'+videoSrc+'" frameborder="0" style="width: 100%; height: 300px;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');    
    this.isVideoOpen = true;
  }
  videoPanelClose(){
    this.iframe = "";
    this.isVideoOpen = false;
  }
  //== Video Section End
}
